<template>
    <div>
        <nav-bar>
            <template v-slot:default>登录</template>
        </nav-bar>

            <div style="text-align: center;padding-top: 50px">
                <van-image
                    width="10rem"
                    height="10rem"
                    fit="contain"
                    src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fbpic.588ku.com%2Felement_origin_min_pic%2F17%2F04%2F10%2F8f0d1ecca24308cc22591a44987dd05c.jpg%21%2Ffwfh%2F804x705%2Fquality%2F90%2Funsharp%2Ftrue%2Fcompress%2Ftrue&refer=http%3A%2F%2Fbpic.588ku.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1616743832&t=7e9c2c97ad90230e1c5abbfda2e7ef60"
                />
            </div>

        <van-form @submit="onSubmit">
            <van-field
                v-model="email"
                name="邮箱"
                label="邮箱"
                placeholder="邮箱"
                :rules="[{ required: true, message: '请填写正确的邮箱' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />

            <div style="margin: 16px;">
                <div class="link-login" @click="$router.push({path:'Register'})">
                    没有账号，立即注册
                </div>
                <van-button round block type="info" color="#ff9000" native-type="submit">登录</van-button>
            </div>
        </van-form>

    </div>
</template>

<script>
    import NavBar from 'components/common/navbar/NavBar.vue';
    import {ref, reactive, toRefs} from 'vue';
    import {login} from '../../network/user';
    import { Notify, Toast } from 'vant';
    import {useRouter} from 'vue-router';
    import {useStore} from 'vuex';

    export default {
        name: 'Login',
        components: {
            NavBar,

        },
        setup() {
            const router = useRouter();
            const store = useStore();

            const userinfo = reactive({
                email: '',
                password: '',
            })

            const onSubmit = () => {
                login(userinfo).then(res => {
                    // 登录成功会将token保存在本地 window LocalStorage   setItem(key, value) grtItem(key)，如果保存在vuex，正常访问没问题，但刷新页面会导致数据初始化，影响用户体验
                    window.localStorage.setItem('token', res.access_token);
                    // 在vuex 临时保存isLogin
                    store.commit('setIsLogin', true);

                    // 登录成功提示
                    Toast.success('登录成功');
                    // 清除userinfo的信息
                    userinfo.email = '';
                    userinfo.password = ''

                    setTimeout( () => {
                        router.go(-1)
                    }, 1300)

                })
            }

            return {
                ...toRefs(userinfo),
                onSubmit,
                
            }
        }
    }
</script>

<style lang="scss" scoped>
.link-login {
    font-size: 14px;
    margin-bottom: 10px;
    text-align: center;
    display: inline-block;
    color: #ff5000;
}
</style>